:root {
    --primary-color: #1086ff;
}

.home-container {
    width: 100vw;
    height: 100vh;
    /* border: 1px solid black; */
    max-width: 600px;
    margin: auto;
    height: calc(var(--vh, 1vh) * 100);
}

.main-chat-container {
    display: flex;
    flex-direction: column;
    border: 1px solid #00a1ff;
    height: 100%;
    position: relative;
}

#signout {
    background: white;
    border: 0px;
    padding: 6px;
    border-radius: 10px;
}

#sending-component {
    display: flex;
    flex-direction: column;
}

.dp {
    border-radius: 50%;
    width: 48px;
    height: -moz-fit-content;
    height: fit-content;
    margin: auto 0;
    border: 2px solid white;
}

.chat-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 65px;
    background: var(--primary-color);
    color: white;
    text-transform: capitalize;
    z-index: 1;
    font-weight: bold;
    box-shadow: 0px 3px 5px 0px #9c9c9c;
    /* border: 1px solid green; */
}

.fa-globe {
    color: #10ff10;
    font-size: 13px;
}

#online-users-list {
    position: absolute;
    font-size: 11px;
    font-weight: normal;
    background: black;
    padding: 10px 20px;
    border: 1px solid white;
    border-radius: 10px;
    display: none;
}

#online-wrapper {
    position: relative;
}

.m-10 {
    display: flex;
    flex-direction: column;
    margin: 10px;
}

.fa-volume-mute,
.fa-volume-up {
    margin: 0 10px;
    padding: 6px 15px;
}

.chat-box {
    height: 100%;
    height: stretch;
    height: -webkit-fill-available;
    /* height: -moz-available; */
    overflow: auto;
    background-image: url(./images/chat.jpg);
    /* background-position: center; */
    background-size: contain;
    scroll-behavior: smooth;
}

#message {
    border-radius: 25px;
    padding: 0 22px;
    width: 100%;
    width: stretch;
    width: -webkit-fill-available;
    width: -moz-available;
    margin: 4px 0px;
    border: 0;
}

#message:focus-visible {
    outline: 0;
}

#message-send-button {
    background: white;
    border: 0px;
    padding: 6px 15px;
    margin: 6px;
    border-radius: 10px;
}

.send-message-wrapper {
    height: 56px;
    justify-content: space-between;
    display: flex;
    padding: 0 10px;
    background: var(--primary-color);
    /* flex-direction: column; */
    /* flex-direction: column; */
    z-index: 1;
    box-shadow: 0px 0px 8px 0px #757575;
    /* border: 1px solid green;*/
}

.reciever-bubble-wrapper {
    width: 100%;
    display: flex;
}

.sender-bubble-wrapper {
    width: 100%;
    justify-content: flex-end;
    display: flex;
}

.message-bubble {
    width: fit-content;
    max-width: 88%;
    min-width: 28%;
    background: #cbeeff;
    margin: 4px;
    border-radius: 10px;
    padding: 12px;
    box-sizing: border-box;
}

.sender {
    background: #00adff;
}

.bubble-name {
    font-weight: bold;
    text-decoration: underline;
    font-size: 11px;
    color: #ac82ff;
}

#typing {
    margin: 0 10px;
    font-style: italic;
    font-size: 11px;
}

#scrollDown {
    display: none;
}

.sender .bubble-name,
.sender .bubble-text,
.sender .bubble-time {
    color: #f1f1f1;
}

.bubble-text {
    color: #404040;
}

.bubble-time {
    float: right;
    font-size: 12px;
    color: #636363;
}

#scrollDown {
    position: absolute;
    bottom: 65px;
    border-radius: 20px;
    right: 20px;
    padding: 10px;
    color: white;
    background: #51bfff;
    border: 1px solid white;
}

#login {
    padding: 28px;
    border-radius: 10px;
    border: 0;
    color: white;
    position: absolute;
    background: #6ebbff;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
}